import React, { useState, useEffect, Fragment } from "react";
import {
  FetchData,
  FetchGeneres,
  FetchMoviesByGeneres,
  SearchMovie,
} from "../api/api";
import Menu from "../optionMenu/optionMenu";
import "./body.css";

import "./flex-scroller.css";
//import "./../discover.css";
//import "./../media.css";
//import "./../index1.css";
//import "./../copy.css";

import Item from "./items";
import FlexScroller from "./flex-scroller";

export default function Body() {
  const [movies, setMovies] = useState([]);
  const [generes, setGeneres] = useState([]);
  const [search, setSearch] = useState("");

  useEffect(() => {
    FetchData().then((result) => setMovies(result));
    FetchGeneres().then((generes) => setGeneres(generes));
    console.log("insdie useEfeect", movies);
    console.log("insdie useEfeect Generes", generes);
  }, []);

  const HandleOnClick = (e) => {
    console.log(e.target.id);
    FetchMoviesByGeneres(e.target.id).then((fetchedMoviesByGenere) =>
      setMovies(fetchedMoviesByGenere)
    );
  };
  const onSubmit = (e) => {
    e.preventDefault();
    SearchMovie(search).then((movies) => setMovies(movies));
  };
  const HandleOnChange = (e) => {
    setSearch(e.target.value);
    console.log(search);
  };

  return (
    <Fragment>
      <section>
        <div className="gender-wrap ">
          <ul class="nav">
            {generes.map((genres) => (
              <li class="nav-item mr-2 mb-2" key={genres.id}>
                <button
                  className="btn badge "
                  id={genres.id}
                  onClick={(event) => HandleOnClick(event)}
                >
                  {genres.name}
                </button>
              </li>
            ))}
          </ul>
        </div>
      </section>

      <section>
        <div class="row mx-auto">
          <form class="form-inline mt-3  mx-auto" onSubmit={(e) => onSubmit(e)}>
            <input
              class="form-control mr-sm-2"
              type="search"
              onChange={(e) => HandleOnChange(e)}
              placeholder="Search"
              aria-label="Search"
            ></input>
            <button class="btn btn-outline-info my-2 my-sm-0" type="submit">
              Search
            </button>
          </form>
        </div>
      </section>

      <FlexScroller movies={movies} />

      <div class="row mt-5">
        {movies.map((movie, index) =>
          movie.poster_path != null ? (
            <Item movie={movie} index={index} />
          ) : (
            <div></div>
          )
        )}
      </div>
    </Fragment>
  );
}
