import React, { useState } from "react";

import { Button, Modal } from "react-bootstrap";

export default function ItemModal(props) {
  const { handleClose, movie, show } = props;
  return (
    <>
      <Modal show={show} onHide={handleClose}>
        <Modal.Body>
          <div className="row">
            <div className="col-6">
              <img
                src={
                  "https://image.tmdb.org/t/p/w220_and_h330_face/" +
                  movie.poster_path
                }
                class="align-self-center mr-3"
                alt="..."
              ></img>
            </div>
            <div className="col-6">
              {movie.title}
              
                <p>
                  Rating:
                  <span class="ml-2 badge badge-dark ">{movie.vote_average}</span>
                </p>
             
            
                <p>
                  Release Date:  
                  <span class="badge badge-dark ml-2 mb-1 ">
                    {movie.release_date}
                  </span>
                </p>
            
              <p> {movie.overview}</p>

              <Modal.Footer>
                <Button variant="closeButton" onClick={(e) => handleClose(e)}>
                  Close
                </Button>
              </Modal.Footer>
            </div>
          </div>
        </Modal.Body>
      </Modal>
    </>
  );
}
