import React from 'react';

export default function FlexScroller(props)
{  const {movies}=props;
    return (
        <section class="inner_content no_pad">
        <div class="column_wrapper">
          <div class="content_wrapper wrap no_bottom_pad">
            <div class="column">
         
              <div
                id="free_scroller"
                class="media discover scroller_wrap should_fade is_fading"
              >
                <div class="column_content flex scroller">
                  {movies.map((movie, index) => (
                    <div class="card style_1 ">
                      <div class="image">
                        <div class="wrapper">
                          <img
                            src={
                              "https://image.tmdb.org/t/p/w220_and_h330_face/" +
                              movie.poster_path
                            }
                          />
                        </div>
                      </div>
                      <div class="content">
                        <div class="consensus tight">
                          
                          <div class="outer_ring">
                            <div class="user_score_chart">
                              <div class="percent">
                                <span class="icon icon-rNR"></span>
                              </div>
                            </div>
                          </div>
                        </div><span className="span-title">{movie.title}</span>
                      </div>
                    </div>
                  ))}
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    )
}