import React, { useState, Fragment } from "react";
import ItemModal from "./itemModal";



export default function Item(props) {
  const [isOpened, setIsOpened] = useState(false);

  const [show, setShow] = useState(false);
    
  const HandleMove=(e)=>{
    e.target.style.cursor = 'pointer';
  };
  const handleClose = (e) => setShow(false);
  const handleShow = (e) => setShow(true);

  const toggle = (e) => {
    setIsOpened((wasOpened) => !wasOpened);
  };

  const { movie, index } = props;

  return (
  <Fragment>
    
  
      
      
      <ItemModal handleClose={handleClose} movie={movie} handleShow={handleShow} show={show}/>
   
   <div className="movie-item  col-sm-2 col-12" key={index}>
      <div className="card"  onMouseMove={HandleMove} onClick={(e) => handleShow(e)}>
        <img
          src={
            "https://image.tmdb.org/t/p/w220_and_h330_face/" + movie.poster_path
          }
        />
        <h5 className=" my-title bold mt-3">{movie.title}</h5>
        

      
      </div>
    </div>
    </Fragment>
  )
}
